.product-search-div {
  margin: 0;
  padding: 20px auto;
  background-color: #195155;
  width: 100%;
  /* box-sizing: border-box; */
}

.search-box {
  margin: 0px auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
}
.input-search-box {
  height: 50px;
  width: 250px;
  border-style: none;
  padding: 10px;
  font-size: 18px;
  letter-spacing: 2px;
  outline: none;
  border-radius: 25px;
  background-color: #1c5155;
  padding-right: 40px;
  color: #fff;
}
.input-search-box::-webkit-input-placeholder {
  color: rgb(255, 255, 255);
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 300;
}
.input-search-box:-ms-input-placeholder {
  color: rgb(255, 255, 255);
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 300;
}
.input-search-box::placeholder {
  color: rgb(255, 255, 255);
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 300;
}

.small-product-img {
  width: 24px;
  height: 24px;
  margin-right: 5px;
}
.search-pro-btn:hover {
  cursor: pointer;
}

